import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import Magazine from '@/components/magazine-item.vue';
import ReadCodeItem from '@/components/read-code-item.vue';
import LoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	data() {
		return {
			title: '肖战-多面棱角蜕变锋芒',
			tabList: [{
					name: '全部杂志'
				},
				{
					name: '我的杂志'
				},
				{
					name: '阅读码'
				}
			],
			tabCur: 0,
			firstMagazine: null,
			allMagazines: [],
			myMagazines: [],
			status: 'more',
			readCodes: [],
			startImgFlag: true,
			allMagPage: 1,
			myMagPage: 1,
			readCodePage: 1,
			loginInfo: {},
			isLogin: false
		}
	},
	onLoad() {
		this.wxLogin();
		setTimeout(() => {
			this.startImgFlag = false;
		}, 3000);
		this.getAllMagList(this.allMagPage);
	},
	onReachBottom() {
		if (this.tabCur == 0) {
			this.allMagPage++;
			this.getAllMagList(this.allMagPage);
		} else if (this.tabCur == 1) {
			this.myMagPage++;
			this.getMyMagList(this.myMagPage);
		} else if (this.tabCur == 2) {
			this.readCodePage++;
			this.getReadCodeList(this.readCodePage);
		}
	},
	methods: {
		tabChange(index) {
			if (this.tabCur == 1) {
				// 如果有了新购买，那么每次都会刷新
				if (this.$store.state.needFresh) {
					this.myMagazines = [];
					this.getMyMagList(1);
				} else if (this.myMagazines.length == 0) {
					this.getMyMagList(1);
				}
			}
			if (this.tabCur == 2) {
				// 如果有了新购买，那么每次都会刷新
				if (this.$store.state.needFresh) {
					this.readCodes = [];
					this.getReadCodeList(1);
				} else if (this.readCodes.length == 0) {
					this.getReadCodeList(1);
				}
			}
		},
		async getAllMagList(page) {
			if (page !== 1) this.status = 'loading';
			let res = await this.myRequest('/api/magazine/index', {
				page,
				per_page: 6
			}, 'POST', false);
			if (res) {
				if (!res.data.data.length) {
					this.status = 'noMore';
					this.allMagPage--;
				} else {
					this.allMagazines = this.allMagazines.concat(res.data.data);
				}
				if (page == 1) {
					this.firstMagazine = this.allMagazines[0];
					this.allMagazines = this.allMagazines.slice(1);
				}
			}
		},
		async getMyMagList(page) {
			if (page !== 1) this.status = 'loading';
			let res = await this.myRequest('/api/magazine/myMafazine', {
				page,
				per_page: 6
			}, 'POST');
			if (res) {
				if (!res.data.data.length) {
					this.status = 'noMore';
					this.myMagPage--;
				} else {
					this.myMagazines = this.myMagazines.concat(res.data.data);
				}
			}
		},
		async getReadCodeList(page) {
			if (page !== 1) this.status = 'loading';
			let res = await this.myRequest('/api/magazine/readCode', {
				page,
				per_page: 6
			}, 'POST');
			if (res) {
				if (!res.data.data.length) {
					this.status = 'noMore';
					this.readCodePage--;
				} else {
					this.readCodes = this.readCodes.concat(res.data.data);
				}
			}
		},
		goUserRank() {
			uni.navigateTo({
				url: `/pages/userrank/userrank?magId=${this.firstMagazine.id}`
			});
		},
		startRead(id) {
			uni.navigateTo({
				url: `/pages/magazinefirst/magazinefirst?magId=${id}`
			});
		},
		wxLogin() {
			let that = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					that.loginInfo.code = loginRes.code;
					console.log(loginRes)
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes)
							that.loginInfo.encryptedData = infoRes.encryptedData;
							that.loginInfo.iv = infoRes.iv;
							that.myLogin();
						},
						fail: function() {}
					})
				}
			});
		},
		async myLogin() {
			let res = await this.myRequest('/api/user/appletLogin', this.loginInfo, 'POST', false);
			if (res && res.error_code == 0) {
				this.$store.commit('saveToken', res.data.token)
				this.$store.commit('saveIsLogin', true);
				this.isLogin = true;
			}
			// console.log(this.$store.state.isLogin)
		},
		onGotUserInfo(res) {
			let infoRes = res.detail
			this.loginInfo.encryptedData = infoRes.encryptedData;
			this.loginInfo.iv = infoRes.iv;
			this.myLogin();
		}
	},
	components: {
		WucTab,
		Magazine,
		LoadMore,
		ReadCodeItem
	},
}
